	module.exports = {"data":{"markdownRemark":{"html":"<h1>Overview</h1>\n<p>iCook is the largest recipe sharing platform in Taiwan. Here I was in the iOS / Ruby on Rails development team, mainly engaged in iCook recipe website (and its related sites).</p>\n<h3>Skills</h3>\n<ul>\n<li>HTML, CSS, JavaScript</li>\n</ul>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1600px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 62.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"icook\"\n        title=\"\"\n        src=\"/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-fe1b4.png\"\n        srcset=\"/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-7e39b.png 400w,\n/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-e636c.png 800w,\n/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-fe1b4.png 1600w,\n/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-ecd44.png 2400w,\n/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-8832c.png 2560w\"\n        sizes=\"(max-width: 1600px) 100vw, 1600px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>I've been in charge prototyping whole website pages and collaborated with designers and engineers. over 4 years I have engaged in projects are:</p>\n<ul>\n<li>iCook - a recipe sharing platform in Taiwan</li>\n<li>Cupboard - iCook's UI Kit which is a front-end style guide</li>\n<li>Newsroom.icook - iCook's official PR website</li>\n<li>Cafe.icook - a forum platform which has been shut down in middle 2015</li>\n</ul>\n<p>You can check <a href=\"https://icook.tw\" target=\"_blank\" rel=\"nofollow noopener noreferrer\">here</a> to see more about iCook.</p>","frontmatter":{"title":"iCook","date":"2017-01-20","client":"iCook","service":"Web development","cover":{"childImageSharp":{"resize":{"src":"/static/icook-890dd7fd13d7d0c3575a8710c7a3d433-f6604.png"}}}},"fields":{"slug":"/i-cook"},"excerpt":"Overview iCook is the largest recipe sharing platform in Taiwan. Here I was in the iOS / Ruby on Rails development team, mainly engaged in…"}},"pathContext":{"slug":"/i-cook"}}

